'use client'

import { useState } from 'react'
import { appLogin } from '@apps-in-toss/web-framework'
import { TossAuthResponse, TossTokenResponse, TossUserInfo, TossLoginResult } from '@/types/toss'
import { TokenManager } from '@/utils/tokenManager'
import { ErrorHandler } from '@/utils/errorHandler'

export const useTossAuth = () => {
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  // 1. í† ìŠ¤ ë¡œê·¸ì¸ (ì¸ê°€ ì½”ë“œ ë°›ê¸°)
  const tossLogin = async (): Promise<TossAuthResponse> => {
    try {
      console.log('[useTossAuth] appLogin í•¨ìˆ˜ í˜¸ì¶œ ì‹œì‘...')
      
      const result = await appLogin()
      console.log('[useTossAuth] appLogin() ì‘ë‹µ:', result)
      
      if (!result.authorizationCode) {
        throw new Error('ì¸ê°€ ì½”ë“œë¥¼ ë°›ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.')
      }
      
      console.log('[useTossAuth] ì¸ê°€ ì½”ë“œ íšë“ ì„±ê³µ:', {
        authorizationCode: result.authorizationCode.substring(0, 20) + '...',
        referrer: result.referrer
      })
      
      return result as TossAuthResponse
    } catch (error) {
      console.error('[useTossAuth] í† ìŠ¤ ë¡œê·¸ì¸ ì‹¤íŒ¨:', error)
      throw error
    }
  }

  // 2. ì•¡ì„¸ìŠ¤ í† í° ë°œê¸‰ (Supabase Edge Function ìš°ì„ , ë°±ì—”ë“œ ëŒ€ì²´)
  const getAccessToken = async (authorizationCode: string, referrer: string): Promise<TossTokenResponse> => {
    try {
      // Supabase Edge Function ìš°ì„  ì‚¬ìš© (mTLS ì§€ì›)
      const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL
      const backendUrl = process.env.NEXT_PUBLIC_BACKEND_URL
      
      const useSupabase = !!supabaseUrl
      const url = useSupabase
        ? `${supabaseUrl}/functions/v1/toss-auth`
        : backendUrl
        ? `${backendUrl}/api/toss/generate-token`
        : 'http://localhost:3001/api/toss/generate-token'
      
      const body = useSupabase
        ? { action: 'generate-token', authorizationCode, referrer }
        : { authorizationCode, referrer }
      
      console.log(`[useTossAuth] ì•¡ì„¸ìŠ¤ í† í° ìš”ì²­ (${useSupabase ? 'Supabase Edge Function (mTLS)' : 'Node.js ë°±ì—”ë“œ'}):`, {
        url,
        method: 'POST',
        body: { authorizationCode: authorizationCode.substring(0, 20) + '...', referrer }
      })
      
      const headers: Record<string, string> = {
        'Content-Type': 'application/json'
      }
      
      if (useSupabase && process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY) {
        headers['Authorization'] = `Bearer ${process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY}`
      }
      
      const response = await fetch(url, {
        method: 'POST',
        headers,
        body: JSON.stringify(body)
      })

      console.log('[useTossAuth] ì•¡ì„¸ìŠ¤ í† í° ì‘ë‹µ:', {
        ok: response.ok,
        status: response.status,
        statusText: response.statusText
      })

      if (!response.ok) {
        const errorText = await response.text()
        console.error('[useTossAuth] í† í° ìš”ì²­ ì‹¤íŒ¨ ì‘ë‹µ:', errorText)
        throw new Error(`í† í° ìš”ì²­ ì‹¤íŒ¨: ${response.status} - ${errorText}`)
      }

      const data = await response.json()
      console.log('[useTossAuth] ì•¡ì„¸ìŠ¤ í† í° ë°œê¸‰ ì„±ê³µ:', data.resultType)
      return data
    } catch (error) {
      console.error('[useTossAuth] ì•¡ì„¸ìŠ¤ í† í° ë°œê¸‰ ì‹¤íŒ¨:', error)
      throw error
    }
  }

  // 3. ì‚¬ìš©ì ì •ë³´ ì¡°íšŒ (Supabase Edge Function ìš°ì„ , ë°±ì—”ë“œ ëŒ€ì²´)
  const getUserInfo = async (accessToken: string, referrer: string): Promise<TossUserInfo> => {
    try {
      // Supabase Edge Function ìš°ì„  ì‚¬ìš© (mTLS ì§€ì›)
      const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL
      const backendUrl = process.env.NEXT_PUBLIC_BACKEND_URL
      
      const useSupabase = !!supabaseUrl
      const url = useSupabase
        ? `${supabaseUrl}/functions/v1/toss-auth`
        : backendUrl
        ? `${backendUrl}/api/toss/user-info`
        : 'http://localhost:3001/api/toss/user-info'
      
      const body = useSupabase
        ? { action: 'get-user-info', accessToken, referrer }
        : { accessToken, referrer }
      
      console.log(`[useTossAuth] ì‚¬ìš©ì ì •ë³´ ìš”ì²­ (${useSupabase ? 'Supabase Edge Function (mTLS)' : 'Node.js ë°±ì—”ë“œ'}):`, {
        url,
        accessToken: accessToken.substring(0, 20) + '...',
        referrer
      })
      
      const headers: Record<string, string> = {
        'Content-Type': 'application/json'
      }
      
      if (useSupabase && process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY) {
        headers['Authorization'] = `Bearer ${process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY}`
      }
      
      const response = await fetch(url, {
        method: 'POST', // Edge Functionì€ POSTë¡œ ì•¡ì…˜ ì „ë‹¬
        headers,
        body: JSON.stringify(body)
      })

      console.log(`[useTossAuth] ì‚¬ìš©ì ì •ë³´ ì‘ë‹µ ìƒíƒœ:`, response.status)

      if (!response.ok) {
        const errorText = await response.text()
        console.error('[useTossAuth] ì‚¬ìš©ì ì •ë³´ ìš”ì²­ ì‹¤íŒ¨:', errorText)
        throw new Error(`ì‚¬ìš©ì ì •ë³´ ìš”ì²­ ì‹¤íŒ¨: ${response.status}`)
      }

      const data = await response.json()
      console.log('[useTossAuth] ì‚¬ìš©ì ì •ë³´ ì¡°íšŒ ì„±ê³µ:', data)
      return data
    } catch (error) {
      console.error('[useTossAuth] ì‚¬ìš©ì ì •ë³´ ì¡°íšŒ ì‹¤íŒ¨:', error)
      throw error
    }
  }

  // í†µí•© ë¡œê·¸ì¸ í•¨ìˆ˜
  const login = async (): Promise<TossLoginResult> => {
    setIsLoading(true)
    setError(null)

    try {
      // 1. í† ìŠ¤ ë¡œê·¸ì¸ (ì¸ê°€ ì½”ë“œ ë°›ê¸°)
      const authResult = await tossLogin()
      
      // 2. ì•¡ì„¸ìŠ¤ í† í° ë°œê¸‰
      const tokenResult = await getAccessToken(
        authResult.authorizationCode,
        authResult.referrer
      )
      
      if (tokenResult.resultType === 'FAILURE') {
        const tossError = ErrorHandler.handleTossError(tokenResult)
        throw new Error(ErrorHandler.getErrorMessage(tossError))
      }

      if (!tokenResult.success) {
        throw new Error('í† í° ë°œê¸‰ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.')
      }

      // 3. ì‚¬ìš©ì ì •ë³´ ì¡°íšŒ
      console.log('[useTossAuth] ì‚¬ìš©ì ì •ë³´ ì¡°íšŒ ì‹œì‘ - referrer:', authResult.referrer)
      const userResult = await getUserInfo(tokenResult.success.accessToken, authResult.referrer)
      
      console.log('[useTossAuth] ì‚¬ìš©ì ì •ë³´ ì¡°íšŒ ê²°ê³¼:', userResult)
      
      if (userResult.resultType === 'FAIL' || userResult.resultType === 'FAILURE') {
        console.error('[useTossAuth] í† ìŠ¤ API ì—ëŸ¬:', userResult.error)
        console.error('[useTossAuth] ì‚¬ìš©ëœ referrer:', authResult.referrer)
        const tossError = ErrorHandler.handleTossError(userResult)
        throw new Error(`ì‚¬ìš©ì ì •ë³´ ì¡°íšŒ ì‹¤íŒ¨ (${authResult.referrer}): ${ErrorHandler.getErrorMessage(tossError)}`)
      }

      if (!userResult.success) {
        throw new Error('ì‚¬ìš©ì ì •ë³´ ì¡°íšŒì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.')
      }

      // 4. í† í° ì €ì¥
      TokenManager.saveTokens(
        tokenResult.success.accessToken,
        tokenResult.success.refreshToken,
        tokenResult.success.expiresIn,
        userResult.success.userKey
      )

      return {
        auth: authResult,
        token: tokenResult.success,
        user: userResult.success
      }
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'ì•Œ ìˆ˜ ì—†ëŠ” ì˜¤ë¥˜'
      setError(errorMessage)
      throw error
    } finally {
      setIsLoading(false)
    }
  }

  // ë¡œê·¸ì•„ì›ƒ
  const logout = () => {
    TokenManager.clearTokens()
    setError(null)
  }

  return {
    login,
    logout,
    isLoading,
    error
  }
}

